// 考试报名表单
<template>
  <div id="BookAnExaln">
    <div class="box">
      <van-form @submit="onSubmit" :show-error-message="false">
        <section>
          <div class="section_title"><span></span> <span>上传照片</span></div>
          <div
            style="background-color: #ffffff"
            class="uploader"
            @click="uploader_fun"
          >
            <div>拍摄或上传2寸证件照。照片用于制作准考证。</div>
            <div>
              <div class="img">
                <img :src="uploader" alt="" />
              </div>

              <van-icon name="arrow" color="#cccccc" size="16" />

              <van-uploader
                :after-read="afterRead"
                style="position: relative"
                ref="uploader"
              />
            </div>
          </div>
        </section>
        <section>
          <div class="section_title"><span></span> <span>基本信息</span></div>
          <van-field
            v-model="username"
            name="真实姓名"
            label="真实姓名"
            placeholder="请输入您的真实姓名"
          />
          <van-field
            v-model="username"
            name="考生性别"
            label="考生性别"
            placeholder="请选择"
            :is-link="true"
            @click="fieldClick('考生性别')"
            readonly 
          />
          <van-field
            v-model="username"
            name="证件类型"
            label="证件类型"
            placeholder="请选择"
            :is-link="true"
            @click="fieldClick('证件类型')"
            readonly 
          />
          <van-field
            v-model="username"
            name="证件号码"
            label="证件号码"
            placeholder="请输入证件号码"
          />
        </section>

        <div style="margin: 16px">
          <van-button round block type="info" native-type="submit"
           @click="next_fun"
            >下一步</van-button
          >
        </div>
      </van-form>
    </div>
    <van-popup v-model="show" position="bottom" :style="{ height: '45%' }">
      <van-picker
        show-toolbar
        :title="picker_title"
        :columns="picker_columns"
        @confirm="picker_confirm"
        @cancel="picker_cancel"
      />
    </van-popup>
  </div>
</template>

<script>
export default {
  name: "BookAnExaln",
  components: {},
  props: {},
  data() {
    return {
      uploader: "",
      username: "",
      password: "",
      show: false,
      picker_columns: [
        {
          text: "浙江",
          children: [
            {
              text: "杭州",
              children: [{ text: "西湖区" }, { text: "余杭区" }],
            },
            {
              text: "温州",
              children: [{ text: "鹿城区" }, { text: "瓯海区" }],
            },
          ],
        },
        {
          text: "福建",
          children: [
            {
              text: "福州",
              children: [{ text: "鼓楼区" }, { text: "台江区" }],
            },
            {
              text: "厦门",
              children: [{ text: "思明区" }, { text: "海沧区" }],
            },
          ],
          picker_title: "",
        },
      ],
    };
  },
  created() {},
  mounted() {
    this.uploader = require("../../../../../assets/images/uploader.png");
  },
  activated() {},
  update() {},
  methods: {
    uploader_fun() {
      this.$refs.uploader.chooseFile();
    },
    afterRead(file) {
      // 此时可以自行将文件上传至服务器
      //   console.log(file.content);
      this.uploader = file.content;
    },
    fieldClick(e) {
      console.log(e);
      if (e == "报考点") {
        this.picker_title = "选择城市";
      }
      this.show = true;
    },
    picker_confirm() {
      console.log("确定");
      this.show = false;
    },
    picker_cancel() {
      console.log("取消");
      this.show = false;
    },
      next_fun(){
      this.$router.push('/enter/informationConfirmation')
    }
  },
  filters: {},
  computed: {},
  watch: {},
};
</script>
<style lang="scss" scoped>
@import "./index.scss";
</style>




